.bar{
    display:flex;
    margin-top: 0px;
    background: #242424;
    height: 80px;
    justify-content:space-between;
}

.bar-text {
    color: #e9dc44;
    font-size: 40px;
    font-weight: 500;
    font-family: 'BebasNeue', sans-serif;
    margin-top:18px;
    margin-left:20px;
}

.project-container {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 3vh;
    background: #f8f8f8;
    padding: 3vh;
    width: 60vw;
}
.project-body {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 2vh;
    background: #d6e8fc;
    padding: 1vh 1vw;
    width: 56vw;   
}

.projectFeature {
    margin-bottom: 1vw;
}
.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
}

.text {
    color: #242424;
    font-size: 36px;
    font-family: 'Inter';
    font-weight: 500;
}

.text2 {
    color: #242424;
    font-size: 24px;
    font-family: 'Inter';
    font-weight: 400;
}

.inputs {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 25px;
    margin-top: 30px;
}

.input-container {
    display:flex;
    flex-direction: column;
    gap: 10px;
}


.input {
    align-items: center;
    margin: auto;
    width: 480px;
    height: 40px;
    border-radius: 6px;
    background: #ffffff;
}

.input input {
    height: 40px;
    width: 480px;
    background: transparent;
    border: none;
    outline: none;
    color: #242424;
    font-size: 20px;
    font-family: 'Inter';
}

.button-container {
    display: flex;
    gap: 50px;
    margin: 50px auto;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 160px;
    height: 59px;
    color: #ffffff;
    background: #242424;
    cursor: pointer;
}

.error-text {
    color: #d70000;
    font-size: 20px;
    font-family: 'Inter';
    font-weight: 400;
    margin:auto;
}


.project-name-container {
    display: flex;
    justify-content: center; /* Center the items horizontally */
    align-items: center; /* Center the items vertically */
    text-align: left; /* Align text to the left within the container */
}
.project-text {
    text-align: center; /* Center the text within this element */
    margin: 0 auto;
    color: #242424;
    font-size: 36px;
    font-family: 'Inter';
    font-weight: 500;
}

.backButton {
    margin-left: 1vw;
    background: #d6e8fc;
    padding: 1vh 1vw;
}