.overlay {
    background: rgba(49, 49, 49, 0.8);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10; /* Ensure it's above other content */
}

.modal-content { /* Renamed from .modal to prevent confusion */
    position: fixed; /* Fixed positioning relative to the viewport */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #f1f1f1;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
    z-index: 20; /* Above the overlay */
}

.content{
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
}

.close{
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
}